body,
html {
  padding: 0px;
  margin: 0px;
}
body {
  font-family: "poppinsregular";
  height: 100%;
}

@font-face {
  font-family: "poppinslight";
  src: url("../fonts/poppins-light-webfont.woff2") format("woff2"),
    url("../fonts/poppins-light-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "poppinsmedium";
  src: url("../fonts/poppins-medium-webfont.woff2") format("woff2"),
    url("../fonts/poppins-medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "poppinsregular";
  src: url("../fonts/poppins-regular-webfont.woff2") format("woff2"),
    url("../fonts/poppins-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "poppinssemibold";
  src: url("../fonts/poppins-semibold-webfont.woff2") format("woff2"),
    url("../fonts/poppins-semibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.back-btn {
  font-size: 16px;
  color: #015eac;
}
.log-out {
  color: #708090;
  font-size: 14px;
  text-decoration: none;
}
.log-out span {
  position: relative;
  top: 3px;
  margin-right: 10px;
}
.log-out img {
}
.header-section {
  /* margin: 40px 0px 40px 0px; */
  margin-top: 20px;
  margin-bottom: 5px;
}
.header-section h2 {
  font-family: "poppinsmedium";
  font-size: 20px;
  color: #001e37;
}
.header-section h4 {
  color: #708090;
  font-size: 14px;
}
.search-wrap {
  position: relative;
  width: 100%;
}
.search-wrap input {
  width: 100%;
  border: 1px solid #70809041;
  border-radius: 25px;
  padding: 10px 50px 10px 20px;
  height: auto;
}
.search-wrap i {
  position: absolute;
  right: 10px;
  top: 7px;
  font-size: 32px;
}
.filter-navigation {
  display: flex;
  margin: 15px 0px;
  align-items: center;
}
.filter-navigation span {
  color: #708090;
  font-family: "poppinslight";
  font-size: 12px;
  margin-right: 3px;
}
.filter-navigation button {
  border: 1px solid #708090;
  border-radius: 5px;
  color: #708090;
  font-family: "poppinslight";
  font-size: 14px;
  padding: 5px 10px;
  margin: 0px 10px 0px 0px;
  background-color: transparent;
  justify-content: space-around;
}
.filter-navigation button:hover {
  background: #015eac0f;
  border: 1px solid #015eac;
  color: #015eac;
  /* font-size: 14px;
    font-family: 'poppinsmedium';
    text-decoration: none; */
}
.active {
  background: #015eac0f;
  /* border: 1px solid #015eac !important; */
  color: #015eac !important;
  font-size: 14px !important;
  font-family: "poppinsmedium" !important;
  text-decoration: none;
}
.country-list {
  position: relative;
  padding: 10px 120px 10px 40px;
  width: 100%;
  display: flex;
  align-items: center;
}
.flag {
  position: absolute;
  left: 0px;
  top: 6px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
}
.flag img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
  top: -3px;
}
.country-list .navigation {
  position: absolute;
  right: 60px;
  top: 6px;
}
.country-list .navigation img {
}
.country-list .bookmarks {
  position: absolute;
  right: 0px;
  top: 6px;
  cursor: pointer;
}
.country-list .bookmarks i {
}
.country-list .bookmarks_click i {
  color: #ccc;
}
.country-list .bookmarks_click_disable i {
  color: #ccc;
}
.country-wrapper {
  width: 100%;
}
.action-wrap {
  margin: 20px 0px 20px 0px;
}
.action-wrap .btn-secondary {
  background: #708090 0% 0% no-repeat padding-box;
  border-radius: 25px;
  opacity: 0.3;
  min-width: 140px;
  position: relative;
}
.action-wrap .btn-secondary i,
.action-wrap .btn-primary i {
  position: absolute;
  font-size: 22px;
  right: 20px;
  top: 7px;
}
.skipbtn {
  position: relative;
  top: 15px;
}
.action-wrap .btn-primary {
  background: #015eac 0% 0% no-repeat padding-box;
  border-radius: 25px;
  min-width: 140px;
  position: relative;
}
.ld_country-list {
  padding: 0px 0px 0px 30px;
}
.ld_form-ctrl {
  background-color: #f5f6f8 !important;
}
.ld_form-ctrl,
.ld_form-ctrl:hover,
.ld_form-ctrl:focus {
  background: #015eac0d 0% 0% no-repeat padding-box;
  border-radius: 3px;
  border: 1px solid #015eac0d;
}
.ld_country-list .flag {
  top: 0px;
}
.form-check-label {
  font-size: 12px;
  color: #708090;
}
.info-wrap {
  position: relative;
}
.info-content {
  display: none;
}
.info-wrap:hover .info-content {
  display: block;
  background: #fffffa 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 3px;
  width: 250px;
  height: auto;
  padding: 15px;
  position: absolute;
  z-index: 2;
  left: 0px;
}
.info-wrap:hover .info-content h4 {
  color: #01aca5;
  font-size: 14px;
}
.info-wrap:hover .info-content p {
  font-size: 13px;
  color: #708090;
}
.user-list {
  margin: 40px 0px;
}
.user-list h3 {
  color: #001e37;
  font-size: 16px;
  margin-bottom: 25px;
}
.user-list a {
  padding: 10px 30px 10px 10px;
  border-radius: 10px;
  color: #001e37;
  font-size: 12px;
  position: relative;
  margin: 0px 0px 0px 10px;
}
.user-list a i {
  color: #708090;
  font-size: 15px;
  position: absolute;
  top: 8px;
  right: 8px;
}
.user-list .odd {
  background: #015eac26;
}
.user-list .even {
  background: #01aca527;
}
.add-btn {
  border-radius: 5px !important;
  min-width: 80px !important;
}
.error {
  background: #ca1a461a 0% 0% no-repeat padding-box !important;
  border: 1px solid #ca1a4641;
  border-radius: 5px;
}
.error i {
  color: red !important;
}
.user-status h4 {
  color: #708090;
  font-size: 12px;
}
.user-status h4 span {
  color: #ca1a46;
}
.black-overlay {
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px;
  position: relative;
}
.msg-wrapper {
  width: 500px;
  text-align: center;
}
.msg-wrapper h3 {
  color: #fff;
  font-family: "poppinsmedium";
}
.msg-wrapper p {
  color: #fff;
  line-height: 35px;
}
.msg-wrapper .btn-primary {
  background: #015eac 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 25px;
  min-width: 320px;
  margin: 10px 0px 0px 0px;
}
.profile-wrap {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  position: absolute;
  top: 15px;
  right: 15px;
}
.profile-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.quick-profile-dropdown {
  width: 320px;
  height: auto;
  background: #fff;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 4px;
  position: absolute;
  top: 55px;
  right: 0px;
  padding: 20px;
}
.profile-name {
  padding: 0px 0px 10px 30px;
  position: relative;
  border-bottom: 1px solid #e2e5e4;
}
.profile-name h4 {
  font-size: 16px;
}
.profile-name h5 {
  font-size: 14px;
  color: #708090;
  font-family: "poppinslight";
}
.profile-name img {
  position: absolute;
  left: -10px;
  top: 2px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #000;
}
.dd-navigation {
  padding: 0px 0px 10px 30px;
  border-bottom: 1px solid #e2e5e4;
}
.dd-navigation a {
  display: block;
  position: relative;
  margin: 22px 0px 10px 0px;
  color: #001e37;
  font-size: 14px;
  font-family: "poppinslight";
}
.dd-navigation a img {
  position: absolute;
  left: -35px;
  top: -5px;
  height: 24px;
  width: 24px;
  background-size: contain;
  object-fit: none;
}
.information-wrap {
  position: absolute;
  right: 20px;
  top: 40px;
  width: 200px;
  text-align: center;
}
.information-wrap h3 {
  color: #ffe280;
  font-size: 15px;
  line-height: 30px;
  margin: 20px 0px 0px 0px;
}
.information-wrap img {
}

.profile {
  /* padding: 5px !important; */
  height: 70px !important;
  width: 70px !important;
  border-radius: 50% !important;
}
/* 
height: 70px;
width: 70px;
border-radius: 50%;
border: 1px solid black;
color: gray; */
.profile img {
  height: 70px;
  width: 70px;
  border-radius: 50%;
  border: 1px solid black;
  color: gray;
}
.pin_icon_active i {
  color: #f00 !important;
}

.upload_img {
  text-decoration: none !important;
  color: gray;
}
.upload_img:hover {
    text-decoration: none !important;
    color: gray;
  }
.pin_active {
  font-size: 20px !important;
  color: #1b9def !important;
}

.pin_normal {
  font-size: 20px !important;
  color: #b7c0c9 !important;
}


.custome-dropdown{
  text-decoration: none !important;
  color: #495057 !important;
  margin-top: 0 !important;
  width: 160px !important;
  border: 1px solid #ced4da !important;
  
}

.custome-dropdown:hover{
  text-decoration: none;
}

.btn-primary:disabled{
  background-color: #a4aeb8 !important;
}
