


.picky__dropdown .option, .picky__dropdown li{
    border-bottom: none !important;
    /* margin-left: 15px !important; */
}

.picky__dropdown .option input[type='checkbox'], .picky__dropdown .option input[type='radio'], .picky__dropdown li input[type='checkbox'], .picky__dropdown li input[type='radio']{
    margin-right: 15px;
}
/* .picky__dropdown{
    padding-left: 15px;
} */

.picky__input{
    background-color: #f5f6f8 !important;
}
.country_dropdown .picky__input{
    border: 1px solid rgba(154, 154, 154, 0.5);
box-sizing: border-box;
border-radius: 6px;
height: 40px;
background-color: #FDFBF7 !important;


}
.country_dropdown .picky__placeholder {
    color: #708090;
    font-size: 12px;
    font-family: "poppinsregular";
}
.picky__dropdown .option{
    /* margin-left: 20px !important; */
}
.picky__input{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 20px;
} 

.picky * {
    margin-left:15px;
}

.picky__dropdown .option:focus, .picky__dropdown li:focus {
    outline: -webkit-focus-ring-color auto 0px !important;

}
.picky__dropdown {
    background: #FDFBF7;
    box-shadow: 0px 2px 2px rgb(0 0 0 / 15%);
    border-radius: 0px 0px 6px 6px;
}

